.subtitle {
  margin: 0 0 5px;
  font-family: $ff-alumni;
  font-weight: 600;

  &--secondary {
    margin-left: 463px;
    font-size: 54px;
    line-height: 64px;
    letter-spacing: 1.62px;

    @include vp-1199 {
      margin-left: 125px;
      font-size: 42px;
      line-height: 52px;
      letter-spacing: 1.26px;
    }

    @include vp-767 {
      margin-left: 72px;
      margin-bottom: 11px;
      font-size: 32px;
      line-height: 26px;
      letter-spacing: 0.96px;
    }
  }

  &--hero {
    margin-bottom: 0;
    padding: 117px 52px 20px;
    font-size: 46px;
    line-height: 46px;
    letter-spacing: 1.44px;
    text-align: right;
    text-transform: lowercase;


    @include vp-1199 {
      padding: 46px 7px 3px;
      font-size: 34px;
      line-height: 48px;
      letter-spacing: 1.02px;
    }

    @include vp-767 {
      padding: 46px 4px 7px;
      font-size: 30px;
      line-height: 46px;
      letter-spacing: .086px;
    }
  }

  &--gym {
    margin-bottom: 26px;
    font-size: 48px;
    line-height: 24px;
    letter-spacing: 1.44px;

    @include vp-1199 {
      margin-bottom: 11px;
      padding: 3px 140px;
      font-size: 42px;
      letter-spacing: 1.26px;
    }

    @include vp-767 {
      margin-bottom: 14px;
      padding: 0;
      font-size: 32px;
      letter-spacing: 0.96px;
      text-align: left;
    }
  }
}
